@import "../../../utils/media.scss";
@import "../../../utils/animations.scss";

.buttonForm {
  position: relative;
  display: block;
  width: 100%;
  width: 180px;
  height: 44px;
  background-color: var(--main-yellow);
  border: none;
  outline: none;
  color: var(--main-black);
  font-family: "Montserrat", sans-serif;
  font-size: 1.3rem;
  font-weight: bold;
  letter-spacing: -0.19px;
  border-radius: 56px;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  z-index: 100;
  transition: all 200ms ease-in-out;
  font-weight: bold;
  &.play::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    width: 15px;
    height: 16px;
    background: url("../../../assets/play-button.svg") center / cover no-repeat;
  }
  &.info {
    width: 140px;
    padding: 14px 27px 14px 44px;
  }
  &.info::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    background: url("../../../assets/more-info.svg") center / cover no-repeat;
  }

  &.hasHover:hover {
    background-color: var(--main-background);
    border: 1px solid var(--main-yellow);
    color: #fff;
  }

  &.small {
    width: 140px;
    height: 35px;
    border-radius: 17.5px;
    font-size: 1.2rem;
    color: var(--main-background);
    &:hover {
      background-color: var(--main-yellow);
      border: none;
    }
  }

  &.purple {
    background-color: var(--main-purple);
    color: var(--main-white);
    &:hover {
      background-color: var(--main-purple);
      border: none;
    }
  }

  &.gray {
    background-color: rgba(#fff, 0.2);
    color: var(--main-white);
    &:hover {
      background-color: rgba(#fff, 0.2);
      border: none;
    }
  }
}
