@import "../../utils/media.scss";

.nav {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 6000;
  transition: all ease-in-out 200ms;

  &.scrolled {
    background: rgba(#11111e, 1);
    transition: all ease-in-out 200ms;
  }

  @include respond(phone) {
    background: #11111e !important;
  }

  &Container {
    max-width: 100%;
    padding: 2rem 58px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;

    @include respond(tab-land) {
      padding: 2rem 38px;
    }

    @include respond(tab-port) {
      padding: 2rem 28px;
    }
    @include respond(phone) {
      padding: 10px 20px;
    }
  }

  &__actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    min-height: 37px;
    max-height: 37px;
    &.isMobile {
      justify-content: space-between;
    }

    &_logo {
      position: relative;
      margin-left: 6rem;
      margin-right: 2rem;

      @include respond(phone) {
        margin: 0;
      }
      &_lottie {
        width: 114px;
        margin-bottom: 10px;
        [stroke="rgb(63,59,105)"],
        [fill="rgb(63,59,105)"] {
          stroke: #fff !important;
          fill: #fff !important;
        }
      }
    }

    &_burger {
      margin-right: 4rem;
      align-self: center;
      cursor: pointer;
    }
  }

  &__winFlixx {
    width: fit-content;
    display: flex;
    justify-content: center;

    @include respond(phone) {
      width: 100%;
    }
  }

  &__user {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include respond(desktop) {
      width: 80%;
    }
    @include respond(tab-land) {
      width: 50%;
    }
    @include respond(tab-port) {
      width: fit-content;
    }
    @include respond(phone) {
      display: none;
    }
  }

  .winFlixx {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.burger {
  position: fixed;
  top: 25px;
  left: 58px;
  z-index: 10000;
  width: 25px;
  cursor: pointer;
}

.isSearching {
  visibility: hidden !important;
  opacity: 0 !important;
}
