@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700,800");
@import "./utils/media.scss";

:root {
  --main-black: #000;
  --main-white: #fff;
  --main-yellow: #ffc404;
  --main-error: #ff0a0a;
  --secondary-yellow: #ffc900;
  --main-purple: #6432d8;
  --main-background: #11111e;
  --text-slide: #f5f5f5;
  --text-description: #a0a1a1;
  --button-black: #211f35;
  --text-placeholder: #a0a1a1;
  --text-input: #707070;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;

  @include respond(phone) {
    font-size: 52%;
  }
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif !important;
  background: #11111e;
}

.focusRing {
  outline-color: none !important;
}

*:focus {
  outline: none;
}

.carousel__dot--selected {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: #fff !important;
  opacity: 1 !important;
  outline: none;
  border: 0;
}

.ModalAlertCarousel_dot__HDhwl.carousel__dot--selected {
  background-color: #ffc900 !important;
}

.carousel__slide-focus-ring {
  @include respond(phone) {
    display: none;
  }
}

button[aria-label="previous"] {
  @media only screen and (min-width: 601px) {
    margin-left: 10px;
  }

  @include respond(phone) {
    display: none;
  }
}

button[aria-label="next"] {
  @media only screen and (min-width: 601px) {
    margin-right: 10px;
  }

  @include respond(phone) {
    display: none;
  }
}

