.TextPrimary {
  font-size: 76px;
  font-weight: 500;
  color: var(--main-white);
  font-family: "Montserrat", sans-serif;
  &.bold {
    font-weight: bold;
  }
  &.semibold {
    font-weight: 600;
  }
  &.regular {
    font-weight: 400;
  }
  &.light {
    font-weight: 300;
  }

  &.black {
    color: var(--main-black);
  }
  &.yellow {
    color: var(--main-yellow);
  }
  &.slide {
    color: var(--text-slide);
  }
  &.description {
    color: var(--text-description);
  }
  &.error {
    color: var(--main-error);
  }
}
