@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@import "../../../utils/media.scss";

.slide {
  animation: fadeIn 1s ease-in-out;
  animation-fill-mode: backwards;

  @include respond(phone) {
    padding-bottom: 100% !important;
  }
}

.serieSlider {
  width: 100%;
  max-width: 245px;
  cursor: pointer;

  &Episode {
    position: absolute;
    bottom: 10px;
    right: 17px;
    color: #fcfcfb;
    font-family: "Montserrat", sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 36px;
    z-index: 100000;
  }

  &Thumbnail {
    width: 100%;
    height: 138px;
    transition: all 200ms ease-in-out;
    position: relative;
    cursor: pointer;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px;
      border: 2px solid transparent;

      &:hover {
      }
    }

    &:hover {
      &::after {
        opacity: 1;
        visibility: 1;
      }
    }
  }

  &Info {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 17px;

    h3,
    h4,
    p {
      text-align: left;
      color: var(--main-white);
      font-family: "Montserrat", sans-serif;
      font-size: 1.2rem;
      letter-spacing: normal;
      font-weight: 600;
    }

    h4 {
      margin-bottom: 8px;
      height: 30px;
    }

    p {
      font-weight: 300;
      height: 80px;
      margin-bottom: 18px;
      max-height: 80px;
      color: var(--text-description);
      overflow: hidden;
    }
    &.isPurchased {
      h3,
      h4 {
        color: #ffc404;
      }
    }
  }

  &Button {
    background-color: #6432d8;
    border: 1.38px solid transparent;
    border-radius: 100px;
    color: #fefefe;
    font-family: "Montserrat", sans-serif;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    font-weight: 500;
    cursor: pointer;
    width: 100px;
    height: 25px;

    &.toPurchase {
      background-color: #ffc404;
      border: 1.38px solid #ffc404;
      color: #11111e;
      font-weight: bold;
    }
  }
}

.gradient {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.9) 0%,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0) 100%
  );
  border-radius: 10px;

  &:hover {
    border: 1.38px solid #ffc404;
  }

  &.isSelected {
    border: 1.38px solid #ffc404;
  }
}
