@import "../../../utils/animations.scss";
@import "../../../utils/media.scss";
.maker {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 100;
  margin: 15px 0;

  &Type {
    color: #fff;
    text-align: left;
    margin: 0 1rem;
  }
  &Bar {
    color: #fff;
    font-family: "Montserrat", sans-serif;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 0.01px;
    text-align: center;
  }
  &Duration {
    display: flex;
    align-items: center;
    color: #ffc404;
    font-family: "Montserrat", sans-serif;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 0.01px;
    text-align: left;
    // margin: 0 1rem;
    // margin-left: 30px;
    &Val {
      font-family: "Montserrat", sans-serif;
      font-size: 1.4rem;
      font-weight: 600;
      letter-spacing: 0.01px;
      text-align: left;
      color: #ffc404;
      opacity: 1;
      margin-right: 1rem;
    }
  }
  &User {
    color: #ffc404;
    font-family: "Montserrat", sans-serif;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 0.01px;

    text-align: left;
    margin: 0 1rem;
    // @include respond(phone) {
    //   margin-left: 0;
    // }
  }
}
