.contentActions {
  display: flex;
  &.series {
    flex-direction: row;
    justify-content: center;
    flex-direction: column;
  }
  &.community {
  }
  &_price {
    display: flex;
    align-items: center;
    span {
      display: inline-block;
    }
  }
  &_flixx {
    // margin-left: 12px;
  }
  &_episode {
    margin-right: 12px;
  }
  &_coin {
    width: 29px;
    height: 29px;
    margin-right: 12px;
  }
  &_actions {
    display: flex;
    margin-left: 18px;
    &.series {
      margin-top: 18px;
      margin-left: 0;
    }
    &_main {
      margin-right: 10px;
      position: relative;
    }
  }
  &_error {
    display: inline-block;
    position: absolute;
    top: 15px;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    transition: all 300ms ease-in-out;
    &.hovered {
      top: 56px;
    }
  }
  .secondary {
    transition: all 300ms ease-in-out;
    &:hover {
      background-color: #46454d;
    }
  }
}
