@import "../../../utils/media.scss";
@import "../../../utils/animations.scss";

.carousel {
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  // height: 659px;
  height: 100%;
  position: relative;

  .contentInfo {
    margin-left: 5rem;
    margin-top: -10vw;
    z-index: 1000;
    @include respond(phone) {
      padding: 0 20px;
      margin-top: 100px;
      width: 100%;
      z-index: 100;
    }
  }

  @include respond(phone) {
    // height: auto;
    // padding-top: 200px;
    height: 340px;
  }

  &Background {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    height: 100%;
    // max-height: 80vh;
    animation: fadeIn 1s ease-in-out;
    cursor: pointer;
    @include respond(phone) {
      height: 220px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      max-width: 100%;
      position: absolute;
      // top: 0;
      top: -8vw;
      right: 0;
      z-index: -1;
      // @include respond(phone) {
      //   object-fit: contain;
      // }
    }
  }

  &LeftGradient {
    width: 60%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 1;
    transition: all 300ms ease-in-out;
    transition-delay: 0.4s;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    // background: linear-gradient(to right, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.8) 50%, rgba(17, 17, 30, 0.6951155462184874) 70%, rgba(17, 17, 30, 0) 100%);
    background-image: linear-gradient(
      to right,
      rgba(17, 17, 30, 0.9) 0%,
      rgba(17, 17, 30, 0.85) 40%,
      rgba(17, 17, 30, 0) 100%
    );
    z-index: 10;
    // &.isHovered {
    //   opacity: 1;
    // }
    @include respond(phone) {
      display: none;
    }
  }
  &TopGradient {
    width: 100%;
    // height: 82px;
    height: 154px;
    position: absolute;
    left: 0;
    top: -30px;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    // background: linear-gradient(to top, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.3) 35%, rgba(0, 0, 0, 0.3) 80%, rgba(0, 0, 0, 0.3) 100%);
    background-image: linear-gradient(
      to bottom,
      rgba(17, 17, 30, 0.9) 25%,
      rgba(17, 17, 31, 0.5) 50%,
      rgba(17, 17, 30, 0)
    );
    z-index: 10;

    @include respond(phone) {
      display: none;
    }
  }

  &BottomGradient {
    position: absolute;
    background-image: linear-gradient(
      to bottom,
      rgba(20, 20, 20, 0) 0,
      rgba(20, 20, 20, 0.15) 15%,
      rgba(20, 20, 20, 0.35) 29%,
      rgba(20, 20, 20, 0.58) 44%,
      #141414 68%,
      #141414 100%
    );
    -webkit-background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    background-size: 100% 100%;
    background-position: 0 top;
    background-repeat: repeat-x;
    background-color: transparent;
    width: 100%;
    height: 14.7vw;
    top: auto;
    bottom: 6vw;
    opacity: 1;
    transition: all 0.4s cubic-bezier(0.665, 0.235, 0.265, 0.8) 0s;
    transition-delay: 0.4s;
    &.isHovered {
      bottom: 7vw;
      height: 18vw;
    }
  }

  // &BottomGradient {
  //   width: 100%;
  //   // height: 82px;
  //   // height: 219px;
  //   height: 154px;
  //   position: absolute;
  //   left: 0;
  //   bottom: 6vw;

  //   // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
  //   // background: linear-gradient(to top, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.3) 35%, rgba(0, 0, 0, 0.3) 80%, rgba(0, 0, 0, 0.3) 100%);
  //   // background-image: linear-gradient(to top, rgba(17, 17, 30, 0.99) 61%, rgba(17, 17, 30, 0));
  //   background-image: linear-gradient(
  //     to top,
  //     rgba(17, 17, 30, 1) 50%,
  //     rgba(17, 17, 31, 0.5) 80%,
  //     rgba(17, 17, 30, 0)
  //   );
  //   z-index: 10;
  //   &.isHovered {
  //     height: 200px;
  //     bottom: 10vw;
  //   }

  //   @include respond(phone) {
  //     height: 240px;
  //     background-image: linear-gradient(
  //       180deg,
  //       rgba(0, 0, 0, 0) 0%,
  //       rgba(17, 17, 31, 0.1) 20%,
  //       rgba(17, 17, 31, 1) 40%
  //     );
  //   }
  // }
}

.hidden {
  transition: opacity 500ms ease-in-out;
  opacity: 0;
}

.visible {
  transition: opacity 500ms ease-in-out;
  opacity: 1;
}

