@import "../../../utils/media.scss";

.userBalance {
  &__svg {
    width: 25px;
    height: 25px;
  }
  &__coins {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1.5rem;

    @include respond(phone) {
      margin-right: 0;
    }

    svg {
      width: 24.66px;
      margin-right: 1rem;
    }

    span {
      color: #fefefe;
      font-family: "Montserrat", sans-serif;
      font-size: 1.6rem;
      font-weight: 600;
      line-height: 19px;
      text-align: left;
    }
  }
}
